import React, { useState } from 'react'
// import Footer from "./Footer"

const Contact = () => {

    const [data , setData] = useState({
        fullname : '',
        phone : '',
        email : '',
        message : ''
    })

    const InputEvent = (event) => {
        const {name , value} = event.target ;
        setData((preVal) => {
            return {
                ...preVal ,
                [name] : value ,
            }
        })

    }
    const formsubmit = (e) => {
        e.preventDefault();
        alert(`My name is ${data.fullname} . My mobile number is  ${data.phone} and my message is ${data.message}`)
    }



    return (<>
        <div className="my-5">
            <h1 className="text-center">Contact US</h1>
        </div>

        <div className="container contact_div">
            <div className="row">
                <div className="col-md-6 col-10 mx-auto">
                    <form onSubmit={formsubmit}>
                        <div class="mb-3">
                            <label for="exampleFormControlInput1"
                             class="form-label">
                             Full Name
                             </label>
                            <input type="text"
                             class="form-control"
                              id="exampleFormControlInput1" 
                              name = "fullname"
                              value = {data.fullname}
                              onChange = {InputEvent}
                              placeholder="Enter your name"  required />
                        </div>

                        <div class="mb-3">
                            <label for="exampleFormControlInput1"
                             class="form-label">
                             Email address
                             </label>
                            <input type="email"
                             class="form-control"
                              id="exampleFormControlInput1" 
                              name = "email"
                              value = {data.email}
                              onChange = {InputEvent}
                              placeholder="Enter your email"  required />
                        </div>

                        <div class="mb-3">
                            <label for="exampleFormControlInput1"
                             class="form-label">
                            Phone number
                             </label>
                            <input type="number"
                             class="form-control"
                              id="exampleInputEmail1" 
                              name = "phone"
                              value = {data.phone}
                              onChange = {InputEvent}
                              placeholder="mobile no." required />
                        </div>


                        <div class="mb-3">
                            <label for="exampleFormControlTextarea1" 
                            class="form-label">
                            Message
                            </label>
                           <textarea 
                           class = "form-control"
                           id = "exampleFormControlTextarea1"
                           rows = "3" 
                           name = "message"
                            value = {data.message}
                            onChange = {InputEvent}
                            required/>
                        </div>
                        {/* <div class="mb-3 form-check">
    <input type="checkbox" class="form-check-input" id="exampleCheck1" />
    <label class="form-check-label" for="exampleCheck1">Check me out</label>
  </div> */}
                        <button type="submit" class="btn btn-outline-primary">Submit Form</button>
                    </form>
                </div>

            </div>

        </div>

       
    </>
    )
}

export default Contact
